.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 10vh;
    background-color: white;
    padding-top: 10px;
    padding-bottom: 2px;
}

.heading {
    align-self: flex-start;
    color: grey;
    font-size: 37px;
    padding-left: 30px;
    font-weight: bold;

}

.span {
    background-color: orange;
    color: white;
    font-size: 37px;
    font-weight: bold;
    padding-left: 7px;
    padding-right: 7px;
}

.search {
    background: #f2f2f2;
    display: flex;
    padding: 4px;
    border-radius: 5px;
    padding-left: 10px;
}

.icon {
    height: 25px;
    width: 25px;
    color:grey;
    cursor: pointer;
}

.searchelement {
    background: #f2f2f2;
    width: 400px;
    padding: 3px;
    border: none;
    padding-left: 10px;
    color:lightgrey;
}

.iconscontainer {
    display: flex;
    margin-right: 2rem;
    padding:12px;
}

.image {
    height: 2rem;
    width: 2rem;
    padding-left: 10px;
    margin-right:12px;
}
.icons {
    padding-right: 20px;
    color: grey;
    cursor: pointer;
}